import { useCart } from 'context/CartContext';
import { usePrice } from 'context/PriceContext';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { countTotalProductWithGivenStatus } from 'utils/wishlistAndProduct';

export const Header = () => {
  const [totalApprovedProduct, setTotalApprovedProduct] = useState<number>(0);

  const { wishlists } = useCart();
  const { totalPrice, totalDiscount } = usePrice();

  useEffect(() => {
    const { count } = countTotalProductWithGivenStatus(wishlists, 'approved');
    setTotalApprovedProduct(count);
  }, [wishlists]);
  return (
    <header className="App-header">
      <h1>Droppe Assignment</h1>
      <div className="total-text-group">
        <Link to="/">
          <button>Home</button>
        </Link>
        <span className="total-price">
          <span>Total</span>
          <span>‚Ç¨{totalPrice.toFixed(2)}</span>
        </span>
        <span className="total-saving">
          <span>Total Saving</span>
          <span>‚Ç¨{totalDiscount.toFixed(2)}</span>
        </span>
        <span className="total-approved-product">
          <span>üõí</span>
          <span>{totalApprovedProduct}</span>
        </span>
        <Link to="/overview">
          <button className="view-cart">View cart</button>
        </Link>
      </div>
    </header>
  );
};
